.app {
  min-height: 100vh;
  background: #f9fafb;
  font-family: 'Times New Roman', serif;
  box-sizing: border-box;
  width: 100vw;
  margin: 0;
  padding: 0;
}

.header {
  background: #fff;
  padding: 1.2rem 2vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0.08rem 0.2rem rgba(0, 0, 0, 0.08);
  font-family: 'Times New Roman', serif;
  width: 100%;
  max-width: 100vw;
  margin: 0;
  box-sizing: border-box;
}

.header-group {
  display: flex;
  gap: 1.2rem;
  align-items: center;
}

.btn {
  padding: 0.6rem 1.2rem;
  border-radius: 0.375rem;
  font-size: 1.05rem;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: background 0.2s;
  font-family: 'Times New Roman', serif;
}

.btn-primary {
  background: #2563eb;
  color: #fff;
}

.btn-secondary {
  background: #f3f4f6;
  color: #374151;
  border: 0.07rem solid #d1d5db;
}

.btn-danger {
  background: #dc2626;
  color: #fff;
}

.content-row {
  display: flex;
  flex-direction: row;
  gap: 2vw;
  padding: 2vw;
  min-height: 80vh;
  align-items: flex-start;
  font-family: 'Times New Roman', serif;
  width: 100vw;
  max-width: 100vw;
  box-sizing: border-box;
  margin: 0;
}

.main-content {
  display: flex;
  flex-direction: column;
  flex: 3;
  gap: 1.5vw;
  min-width: 0;
  font-family: 'Times New Roman', serif;
  width: 65vw;
  max-width: 70vw;
  box-sizing: border-box;
}

.graph-panel,
.plot2d-panel {
  background: #fff;
  border-radius: 1rem;
  padding: 1.2vw;
  box-shadow: 0 0.08rem 0.2rem rgba(0, 0, 0, 0.08);
  margin-bottom: 1vw;
  font-family: 'Times New Roman', serif;
  min-width: 0;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  gap: 2vw;
}

.dropdown-container {
  display: flex;
  align-items: center;
  gap: 1vw;
  font-size: 1.1rem;
  font-family: 'Times New Roman', serif;
}

#section-select {
  font-size: 1.1rem;
  padding: 0.6rem 1.2rem;
  border-radius: 0.375rem;
  border: 0.07rem solid #d1d5db;
  background: #fff;
  font-family: 'Times New Roman', serif;
}

.plot2d-radio-group {
  display: flex;
  gap: 2vw;
  margin-bottom: 1vw;
  align-items: center;
  font-family: 'Times New Roman', serif;
  flex-wrap: wrap;
}

.plot2d-radio-group label {
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.7vw;
  font-size: 1.1rem;
  font-family: 'Times New Roman', serif;
}

.plot2d-radio-group input[type="radio"] {
  accent-color: #2563eb;
}

.side-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 1vw;
  align-items: flex-end;
  font-family: 'Times New Roman', serif;
  min-width: 260px;
  max-width: 30vw;
  box-sizing: border-box;
}

.side-panel {
  display: flex;
  flex-direction: column;
  gap: 1.5vw;
  width: 100%;
  font-family: 'Times New Roman', serif;
  align-items: center;
}

.specs-panel,
.controls-panel {
  background: linear-gradient(135deg, #f3f4f6 60%, #e0e7ff 100%);
  border-radius: 1.2rem;
  box-shadow: 0 0.15rem 0.4rem rgba(37, 99, 235, 0.08), 0 0.05rem 0.2rem rgba(0, 0, 0, 0.04);
  padding: 2vw 1.5vw 1vw 1.5vw;
  font-family: 'Times New Roman', serif;
  border: 0.07rem solid #d1d5db;
  transition: box-shadow 0.2s;
  min-width: 300px;
  max-width: 400px;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
}

.specs-panel:hover,
.controls-panel:hover {
  box-shadow: 0 0.25rem 0.7rem rgba(37, 99, 235, 0.18), 0 0.1rem 0.3rem rgba(0, 0, 0, 0.08);
}

.specs-title,
.controls-title {
  font-weight: 700;
  margin-bottom: 1.5vw;
  font-size: 2rem;
  color: #2563eb;
  text-align: center;
  font-family: 'Times New Roman', serif;
  letter-spacing: 0.05em;
}

.specs-table,
.parameters-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1vw;
  font-family: 'Times New Roman', serif;
  table-layout: fixed;
}

.specs-table td,
.parameters-table td,
.parameters-table th {
  padding: 0.7vw 0.5vw;
  font-size: 1.1rem;
  font-family: 'Times New Roman', serif;
  word-break: break-word;
  overflow-wrap: break-word;
}

.parameters-table th.text-left,
.parameters-table td:first-child {
  width: 35%;
}

.parameters-table th:nth-child(2),
.parameters-table td:nth-child(2) {
  width: 32%;
}

.parameters-table th:nth-child(3),
.parameters-table td:nth-child(3) {
  width: 33%;
}

.input-field {
  width: 100%;
  min-width: 4em;
  max-width: 100%;
  padding: 0.3vw 0.7vw;
  border: 0.07rem solid #d1d5db;
  border-radius: 0.3rem;
  font-size: 1.1rem;
  background: #f3f4f6;
  font-family: 'Times New Roman', serif;
  box-sizing: border-box;
}

.computation-controls {
  display: flex;
  gap: 1vw;
  margin-bottom: 1vw;
  font-family: 'Times New Roman', serif;
}

/* Plotly canvas adjustments */
.js-plotly-plot {
  width: 100% !important;
  min-height: 350px;
  max-height: 450px;
  background: transparent !important;
  font-family: 'Times New Roman', serif;
  box-sizing: border-box;
}

/* Add these styles to your GeometryModule.css file */

.file-visibility-container {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 8px;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  cursor: pointer;
}

.checkbox-label input[type="checkbox"] {
  margin: 0;
}

.dropdown-container {
  margin: 10px 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

.dropdown-container label {
  font-weight: bold;
  min-width: 100px;
}

.dropdown-container select {
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}


/* Add this to your GeometryModule.css file */

.selection-info {
  margin-bottom: 1rem;
  padding: 0.8rem;
  background-color: #e3f2fd;
  border-left: 4px solid #2563eb;
  border-radius: 0.375rem;
  text-align: center;
}

.selection-header {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
  color: #2563eb;
  font-family: 'Times New Roman', serif;
}


/* Responsive */
@media (max-width: 1200px) {
  .content-row {
    flex-direction: column;
    gap: 2vw;
    padding: 2vw;
    min-height: unset;
    max-width: 100vw;
  }

  .main-content,
  .side-content {
    width: 100%;
    min-width: unset;
    gap: 2vw;
    align-items: stretch;
    max-width: 100vw;
  }

  .side-panel {
    width: 100%;
    min-width: unset;
    max-width: unset;
    margin-top: 2vw;
    align-items: center;
  }

  .specs-panel,
  .controls-panel {
    min-width: unset;
    max-width: 400px;
    margin: 0 auto 2vw auto;
  }

  .graph-panel,
  .plot2d-panel {
    min-width: unset;
    margin: 0 auto 2vw auto;
  }
}

@media (max-width: 900px) {
  .header {
    flex-direction: column;
    padding: 1vw;
    gap: 1vw;
    max-width: 100vw;
  }

  .content-row {
    flex-direction: column;
    gap: 2vw;
    padding: 1vw;
    max-width: 100vw;
  }

  .main-content {
    width: 100%;
    min-width: unset;
    gap: 2vw;
    align-items: stretch;
    max-width: 100vw;
  }

  .side-content {
    max-width: 100vw;
    min-width: unset;
    align-items: center;
  }

  .side-panel {
    width: 100%;
    min-width: unset;
    max-width: unset;
    margin-top: 2vw;
    align-items: center;
  }

  .specs-panel,
  .controls-panel {
    min-width: unset;
    max-width: 100vw;
    margin: 0 auto 2vw auto;
    padding: 4vw 2vw 2vw 2vw;
  }

  .graph-panel,
  .plot2d-panel {
    min-width: unset;
    margin: 0 auto 2vw auto;
    padding: 4vw 2vw 2vw 2vw;
  }

  .specs-title,
  .controls-title {
    font-size: 1.5rem;
  }

  .specs-table td,
  .parameters-table td,
  .parameters-table th {
    padding: 1vw 2vw;
    font-size: 1rem;
  }

  .input-field {
    font-size: 1rem;
    padding: 0.7vw 1vw;
  }

  .js-plotly-plot {
    min-height: 200px;
    max-height: 300px;
  }
}